import { useState } from "react";
import styled from "styled-components";
import {useProgress} from "../../../hooks/useProgress";
import { openLink } from "../../../utils/openLink";
import { VK_LINK } from "../../../constants";
import { Timer } from "../../shared/timer";
import { MergeGame } from "../../shared/merge-game";
import { Block } from "../../shared/block";
import { Title, Text, AddText } from "../../shared/texts";
import { DarkenBg } from "../../shared/darken-bg";
import { Button } from "../../shared/button";
import { results, cards } from './constants';

const FinishedWrapper = styled(DarkenBg)`
    z-index: 10;
    padding: var(--screen_padding);
`;

const ButtonStyled = styled(Button)`
    margin: calc(var(--screen_padding) * 1.5) auto calc(var(--screen_padding) * 2);

    &:last-child {
        margin-bottom: 0;
    }
`;

const BlockStyled = styled(Block)`
    margin-top: min(80px, 21vw);
`;

const RulesBlock = styled(Block)`
    margin-top: min(13.6vw, 62px);
`;

export const Screen4 = () => {
    const { next } = useProgress();
    const [isFinished, setIsFinished] = useState(false);
    const [isStart, setIsStart] = useState(true);

    const handleNext = () => {
        next();
    };

    const handleStartGame = () => {
        setIsStart(false);
    };

    const handleOpenLink = () => {
        openLink(VK_LINK);
    };
    
    return (
        <>
            <MergeGame 
                cards={cards} 
                results={results} 
                isShownDarken={!isFinished && !isStart} 
                onFinish={() => setIsFinished(true)} 
                initialTime={60}
                isSecond
            />
            {isStart && (
                <FinishedWrapper>
                    <Timer initialTime={60} />
                    <RulesBlock>
                        <Title>-  !</Title>
                        <Text>
                            {
                                '  ,    ' +
                                ' ,       ! '+
                                '\n'
                            }
                                   <b> 1 </b>.
                        </Text>
                        <ButtonStyled onClick={handleStartGame} bg="blue"></ButtonStyled>
                    </RulesBlock>
                </FinishedWrapper>
            )}
            {isFinished && (
                <FinishedWrapper>
                    <BlockStyled>
                        <Title> !</Title>
                        <Text>
                            {
                                ',  ,  ' + 
                                '  ,  ,     ! ' + 
                                '   ?'
                            }
                        </Text>
                        <ButtonStyled type="light" bg="green" onClick={handleNext}></ButtonStyled>
                        <Text>
                            {
                                '         , ' + 
                                '      ,  :'
                            }
                        </Text>
                        <ButtonStyled type="dark" bg="red" onClick={handleOpenLink}> </ButtonStyled>
                    </BlockStyled>
                    <AddText>.    .  9705152344</AddText>
                </FinishedWrapper>
            )}
        </>
    );
};
